import {useState, useEffect} from 'react'; // adding Network logic to our function not class componenets
import {useParams} from 'react-router-dom';
import {articles} from './article-content';
import {NotFoundPage} from './NotFoundPage';

export const ArticlePage =()=>{
    const[upvotes, setUpvotes] = useState(0);
    const [comments, setComments] = useState([]);

    const {name} = useParams();
    const [text, setText] = useState('');
    const [postedBy, setPostedBy]= useState('');

    const article = articles.find(article => article.name == name);
    useEffect(()=>{
        const loadArticleInfo = async()=>{
            //load data;
            const response = await fetch(`/api/articles/${name}`);
            const data = await response.json();
            const {upvotes =0, comments=[]} = data;
            setUpvotes(upvotes);
            setComments(comments);
        }
        loadArticleInfo();
    }, [name]);

    const addupvote = async()=>{
        const response = await fetch(`/api/articles/${name}/upvotes`,{
            method:'post',
        });
        const data = await response.json();
        const {upvotes} = data;
        setUpvotes(upvotes);
    };

    const addComment = async ()=> {
        const response = await fetch(`/api/articles/${name}/comments`,{
            method:'post',
            body: JSON.stringify({postedBy, text}),
            headers:{
                'Content-Type': 'application/json',
            }
        });
        const data = await response.json();
        const{comments} = data;
        setComments(comments);
        setPostedBy('');
        setText('');
    };

    return article ?(
        <>
            <h1>{article.title}</h1>
            <div id="upvotes-section">
                <button onClick={addupvote}>Add Upvote</button>
                <p>This article has {upvotes} upvotes</p>
            </div>
            {article.content.map((paragraph, i)=>(
                <p key={i}>{paragraph}</p>
            ))}
            <h3> Comments:</h3>
            <div id="add-comment-form">
                <h3>Add a comment</h3>
                <label>
                    Name:
                    <input type="text" value={postedBy} onChange={e=> setPostedBy(e.target.value)} />
                </label>
                <label>
                    Comment:
                    <textarea rows="4" cols="50" value ={text} onChange={e=> setText(e.target.value)} />
                </label>
                <button onClick={addComment}>Add Coment</button>
            </div>
            {comments.map((comment,i)=>(
                <div className="comment" key={i}>
                    <h4>{comment.postedBy}</h4>
                    <p>{comment.text}</p>
                </div>
            ))}
        </>

    ):(
        <NotFoundPage/>
    );
}