import {Link, link} from 'react-router-dom';

/* Use Links tag instead of anchor tag a , 
since we have all the code in our browser and dont need to call server   
With Anchor tag the server trip is made. With Link is SPA*/

export const NavBar =()=>{
    return(
        <nav>  
            <ul>
                <li>
                    <Link to="/">Home</Link>
                </li>
                <li>
                    <Link to="/about">About</Link>
                </li>
                <li>
                    <Link to="/articles">Articles</Link>
                </li>
            </ul>
        </nav>       
    );
}